import { useState, useEffect } from 'react';
import { Grid, Typography, Box, FormControl, InputAdornment, OutlinedInput, Button } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { toastService } from '../services/toasterService'

// assets
import { SearchOutlined } from '@ant-design/icons';

// services
import { leadService } from '../services/leadService'


import ClearRoundedIcon from '@mui/icons-material/ClearRounded';
import Checkbox from '@mui/material/Checkbox';
import ModeEditOutlineOutlinedIcon from '@mui/icons-material/ModeEditOutlineOutlined';
import IconButton from '@mui/material/IconButton';
import Tooltip from '@mui/material/Tooltip';

import { useSelector, useDispatch } from 'react-redux';
import { activeItem } from '../store/reducers/menu';



// ==============================|| DASHBOARD - DEFAULT ||============================== //

const label = { inputProps: { 'aria-label': 'Checkbox demo' } };

const Drafts = () => {
    const [list, setList] = useState([]);
    const navigate = useNavigate();
    const dispatch = useDispatch();

    const [selectAll, setSelectAll] = useState(false);

    const handleCheckboxChange = (index) => {
        const updatedCheckboxes = [...list];
        updatedCheckboxes[index].checked = !updatedCheckboxes[index].checked;
        setList(updatedCheckboxes);
        setSelectAll(updatedCheckboxes.every((ele) => ele.Checked));
    };

    const handleSelectAllChange = () => {
        console.log(list);
        const updatedCheckboxes = list.map((ele) => {
            return { ...ele, checked: !selectAll }
        })

        console.log(updatedCheckboxes);
        setList(updatedCheckboxes);
        setSelectAll(!selectAll);
    };

    useEffect(() => {
        fetchData();
    }, []);

    const fetchData = async () => {
        try {
            const response = await leadService.getDraftedLeads({ draft: true });
            const selectedList = response.map((ele) => {
                return { ...ele, checked: false }
            })
            setList(selectedList);
        } catch (error) {
            // Handle error
        }
    };

    // search function
    const handleChange = async (arg) => {
        const response = await leadService.getDraftedLeads({ search: arg, draft: true });
        setList(response)
    }

    function debounce(cb, delay) {
        let timer;
        return function (...args) {
            clearTimeout(timer)
            timer = setTimeout(() => cb(...args), delay)
        }
    }
    let handle = debounce(handleChange, 500);

    const deleteLead = async (leadId) => {
        const response = await leadService.deleteLead(leadId);
        if (response) {
            toastService.success(response);
            fetchData()
        }
    }

    const updateLead = async (leadId) => {
        dispatch(activeItem({ openItem: ['Supply-Chain'] }));
        navigate(`/supply-chain/${leadId}`)
    }

    const publish = async () => {
        const leads = list.filter(({ _id, checked }) => {
            return checked;
        }).map(({ _id }) => {
            return _id;
        });
        const response = await leadService.publishLeads(leads);
        if (response) {
            toastService.success(response);
            fetchData()
        }
    }

    return (
        <Grid container rowSpacing={4.5} columnSpacing={2.75}>
            <Grid item xs={12} sx={{ mb: -2.25 }}>
                <Grid container sx={{ mb: 2 }}>
                    <Grid item xs={3} sx={{ display: 'flex', alignItems: 'center' }} >
                        <Typography variant='h5'>
                            Search For Draft
                        </Typography>
                    </Grid>
                    <Grid item xs={9}>
                        <Box >
                            <FormControl fullWidth >
                                <OutlinedInput
                                    size="small"
                                    id="header-search"
                                    startAdornment={
                                        <InputAdornment position="start" >
                                            <SearchOutlined />
                                        </InputAdornment>
                                    }
                                    onChange={(e) => handle(e.target.value)}
                                    aria-describedby="header-search-text"
                                    inputProps={{
                                        'aria-label': 'weight'
                                    }}
                                    placeholder="Ctrl + K"
                                    sx={{ backgroundColor: 'rgba(255, 255, 255)' }}
                                />
                            </FormControl>
                        </Box>
                    </Grid>
                </Grid>
                <Grid container sx={{ mb: 2 }}>
                    <Grid item xs={3} sx={{ display: 'flex', alignItems: 'center' }} >
                        <Typography variant='h5'>
                        </Typography>
                    </Grid>
                    <Grid item xs={9} container justifyContent="flex-end" alignItems="center" sx={{ mt: 0, pt: 0 }}>
                        <Tooltip title="Select All">
                            <Checkbox {...label} size='small' label='publish' checked={selectAll} onChange={handleSelectAllChange} />
                        </Tooltip>
                        <Button onClick={publish}>
                            Publish
                        </Button>
                        <Tooltip title="Delete">
                            <IconButton onClick={() => deleteLead()}>
                                <ClearRoundedIcon style={{ color: 'gray' }} sx={{ fontSize: '1.2rem', p: 0 }} />
                            </IconButton>
                        </Tooltip>
                    </Grid>
                </Grid>
                {
                    list?.map((item, index) => {
                        return (
                            <Box sx={{ border: '1px solid #ccc', padding: 2, borderRadius: 2, mb: 2, backgroundColor: 'rgba(255, 255, 255)' }}>
                                <Grid container sx={{ pt: 0 }}>
                                    <Grid item xs={12} spacing={0.5} container justifyContent="flex-end" alignItems="center" sx={{ height: '0.3rem', mt: 0, pt: 0 }}>
                                        <Tooltip title="Select">
                                            <Checkbox {...label} size='small' label='publish'
                                                checked={item.checked}
                                                onChange={() => handleCheckboxChange(index)}

                                            />
                                        </Tooltip>
                                        <Tooltip title="Update">
                                            <IconButton onClick={() => updateLead(item._id)}>
                                                <ModeEditOutlineOutlinedIcon style={{ color: 'gray' }} sx={{ fontSize: '1.2rem', p: 0 }} />
                                            </IconButton>
                                        </Tooltip>
                                        <Tooltip title="Delete">
                                            <IconButton onClick={() => deleteLead(item._id)}>
                                                <ClearRoundedIcon style={{ color: 'gray' }} sx={{ fontSize: '1.2rem', p: 0 }} />
                                            </IconButton>
                                        </Tooltip>
                                    </Grid>
                                    {/* <Grid item xs={4} sm={2} sx={{ p: 0, h: 100 }}>
                    <Box sx={{}}>
                      <Typography>
                        Product:
                      </Typography>
                      <Typography>
                        Quantity:
                      </Typography>
                      <Typography>
                        HSN Number:
                      </Typography>
                      <Typography >
                        Specification:
                      </Typography>
                      <Typography>
                        Note: {item.note}
                      </Typography>
                    </Box>
                  </Grid>
                  <Grid item xs={8} sm={10} sx={{ p: 0, h: 100, width: "5px" }}>
                    <Box sx={{}}>
                      <Typography>
                        {item.product}
                      </Typography>
                      <Typography>
                        {item.quantity}
                      </Typography>
                      <Typography>
                        {item?.hsnNumber}
                      </Typography>
                      <Typography variant="body1">
                        {item.specification}
                      </Typography>
                    </Box>
                  </Grid> */}
                                    <Grid item xs={4} sm={2} sx={{ py: 0, my: 0 }}>
                                        <Typography>
                                            Product:
                                        </Typography>
                                    </Grid>
                                    <Grid item xs={8} sm={10} sx={{ py: 0, my: 0, width: "5px" }}>
                                        <Typography>
                                            {item.product}
                                        </Typography>
                                    </Grid>
                                    <Grid item xs={4} sm={2} sx={{ p: 0, h: 100 }}>
                                        <Typography>
                                            Quantity:
                                        </Typography>
                                    </Grid>
                                    <Grid item xs={8} sm={10} sx={{ p: 0, h: 100, width: "5px" }}>
                                        <Typography>
                                            {item.quantity}
                                        </Typography>
                                    </Grid>
                                    <Grid item xs={4} sm={2} sx={{ p: 0, h: 100 }}>
                                        <Typography>
                                            HSN Number:
                                        </Typography>
                                    </Grid>
                                    <Grid item xs={8} sm={10} sx={{ p: 0, h: 100, width: "5px" }}>
                                        <Typography>
                                            {item?.hsnNumber}
                                        </Typography>
                                    </Grid>
                                    <Grid item xs={4} sm={2} sx={{ p: 0, h: 100 }}>
                                        <Typography >
                                            Specification:
                                        </Typography>
                                    </Grid>
                                    <Grid item xs={8} sm={10} sx={{ p: 0, h: 100, width: "5px" }}>
                                        <Typography variant="body1">
                                            {item.specification}
                                        </Typography>
                                    </Grid>
                                    <Grid item xs={4} sm={2} sx={{ p: 0, h: 100 }}>
                                        <Typography>
                                            Note:
                                        </Typography>
                                    </Grid>
                                    <Grid item xs={8} sm={10} sx={{ p: 0, h: 100, width: "5px" }}>
                                        <Typography variant="body1" sx={{ textWrap: 'wrap' }}>
                                            {item.note}
                                        </Typography>
                                    </Grid>
                                    {/* <Grid item sm={4} sx={{ p: 0, h: 100 }}>
                    <Box sx={{ py: 3, width: '100%', height: "100%" }}>
                      <Typography>
                        buyer : {item.buyer?.name}
                      </Typography>
                      <Typography>
                        email : {item.buyer?.email}
                      </Typography>
                      <Typography>
                        phone : {item.buyer?.phone}
                      </Typography>
                    </Box>
                  </Grid> */}
                                </Grid>
                            </Box>)
                    }
                    )
                }
            </Grid>
        </Grid>
    );
};



export default Drafts;